    onHideUploader(){
        this.setState({
            showlogoUploader: false,
            showUploader: false,
        })
    }

    beforeUpload(billId, fullPath, file, fileList) {  
        // idpath
        let that = this;
        if(fileList.length > 0){
            toast({content:that.state.json['10100ORG-000052'],color:'warning'});/*  logo*/
            return false;
        }
        let isJPG = false;
        if(file.type === 'image/jpeg' || file.type === 'image/png'){
            isJPG = true;
        }

        if (!isJPG) {
            toast({content:that.state.json['10100ORG-000053'],color:'warning'});/*  jpg,png*/
        }
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (!isLt2M) {
            toast({content:that.state.json['10100ORG-000054'],color:'warning'});/*  2M*/
        }
        return isJPG && isLt2M;
        //  return false   return true 
    }

    afterInnercustEvent(props, moduleId, key, value,oldValue){
        let meta = props.meta.getMeta();
        meta['innercustsupp'].items.map((obj) => {
            if(obj.attrcode == 'pk_org'){
                //
                ajax({
                    url:'/nccloud/uapbd/org/checknodeoperapower.do',
                    data:{pk_org: value.value},
                    success:(res)=>{
                        if(res.success){
                            //
                            if(res.data){
                                toast({content:res.data,color:'warning'});
                            }
                            meta['innercustsupp'].items.find((item) => item.attrcode == 'pk_custclass').queryCondition =() =>{
                                return {
                                    pk_org:value.value
                                }
                            }
                            //
                            meta['innercustsupp'].items.find((item) => item.attrcode == 'pk_supplierclass').queryCondition =() =>{
                                return {
                                    pk_org:value.value
                                }
                            }
                        }
                    },
                });
            }
        })
        props.meta.setMeta(meta);
    }

