function  toggleShow (props,enablestate){
    if(enablestate =='2'){//
        props.button.setButtonsVisible({
            disable:true,
            enable:false,
        });
    }else{
        props.button.setButtonsVisible({
            disable:false,
            enable:true,
        });
    }
}

