import {ajax, base, toast,cardCache,promptBox } from 'nc-lightapp-front';
let {setDefData, getDefData } = cardCache;
export default  function(gridId,props,id){

    let _this = this;
    var pk_org = props.getUrlParam('pk_org')
    if(undefined == pk_org){
        pk_org = getDefData('orgunit_pk_org', _this.config.datasource);//cacheTools.get('orgunit_pk_org');
    }
    var status = props.getUrlParam('status')
    
    if(undefined == pk_org ){
        toast({content : _this.state.json['10100ORG-000000'],color : 'warning'});/*  */
    }
    var orgtype = "";

    //
    _this.config.orgtypearr.map((obj)=>{
        if(obj.subGrid === gridId){
            orgtype = obj.pk_orgtype;
        }
    })
    let buttonname = '' ;
    //
    //enabledisable
    if(id.indexOf("enable") != -1){
        buttonname = id.substring(0,id.indexOf("enable"));
        id = 'enable';
    }else{
        buttonname = id.substring(0,id.indexOf("disable"));
        id = 'disable';
    }
    //
    let enable = props.form.getFormItemsValue(gridId,'enablestate');
    switch (id) {
        // 
        case 'enable':
            if(enable.value == 2){
                toast({content : _this.state.json['10100ORG-000031'],color : 'warning'});/*  */
                break;
            }
            promptBox({
                color:'warning',// "success" "success"/"info"/"warning"/"danger",
                title: _this.state.json['10100ORG-000001'],// , ""/""/""/"",/*  */
                noFooter: false,// (),(false),
                content: _this.state.json['10100ORG-000032'],/*  */
                beSureBtnClick: () =>{
                    ajax({
                        url:'/nccloud/uapbd/org/orgtypeenbale.do',
                        data:{pk_org:pk_org,orgtype:orgtype,enable:'2'},
                        success:(res)=>{
                            let {success,data} = res;
                            if(success){
                                _this.config.orgtypearr.map((obj)=>{
                                    props.form.setFormStatus(obj.subGrid,'browse');
                                })
                                toast({ color: 'success', title: _this.state.json['10100ORG-000018'] });/*  */
                                props.form.setFormItemsValue(gridId,{'enablestate':{value:'2',display:_this.state.json['10100ORG-000017']}});/*  */
                                //
                                toggleShow(props,'2',buttonname);
                            };
                        }
                    });
                }   
            })

            break;
        case 'disable':
            if(enable == 2){
                toast({content : _this.state.json['10100ORG-000033'],color : 'warning'});/*  */
                break;
            }
            promptBox({
                color:'warning',// "success" "success"/"info"/"warning"/"danger",
                title: _this.state.json['10100ORG-000001'],// , ""/""/""/"",/*  */
                noFooter: false,// (),(false),
                content: _this.state.json['10100ORG-000034'],/*  */
                beSureBtnClick: () =>{
                    ajax({
                        url:'/nccloud/uapbd/org/orgtypeenbale.do',
                        //data:formdata,
                        data:{pk_org:pk_org,orgtype:orgtype,enable:'3'},
                        success:(res)=>{
                            let {success,data} = res;
                            if(success){
                                _this.config.orgtypearr.map((obj)=>{
                                    props.form.setFormStatus(obj.subGrid,'browse');
                                })
                                toast({ color: 'success', title: _this.state.json['10100ORG-000014'] });/*  */
                                //
                                props.form.setFormItemsValue(gridId,{'enablestate':{value:'3',display:_this.state.json['10100ORG-000013']}});/*  */
                                //
                                toggleShow(props,'3',buttonname);
                            };
                        }
                    });
                }   
            })
            break;
        default:
            break;
    }
}

//--
function  toggleShow (props,enablestate,buttonname){
    if(enablestate ==='2'){//
        props.button.setButtonVisible([buttonname + 'disable'],true);
        props.button.setButtonVisible([buttonname + 'enable'],false);
    }else{
        props.button.setButtonVisible([buttonname + 'disable'],false);
        props.button.setButtonVisible([buttonname + 'enable'],true);
    }
}
